export const required = (value) => {
   if (value) return undefined      //  value 
   return "Fieled is required"       //  value 
}

//  - thunk,           
export const maxLengthCreator = (maxLength) => (value) => {
   if (value.length > maxLength) return `Max length is ${maxLength} symbols`     //   > 30  
   return undefined        // 
}